@import "src/styles/global.scss";
.product {
  padding-block: 64px 100px;

  &__wrapper {
    display: flex;
    margin-bottom: 42px;
    gap: 30px;
  }

  &__main {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    gap: 30px;
  }

  &__top {
    display: flex;
    border-radius: 4px;
    padding: 24px 20px;
    background-color: $color-bg-accent;
    gap: 30px;
  }

  &__images {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__main-img {
    width: 390px;
    height: 300px;
    -o-object-fit: contain;
       object-fit: contain;
    -o-object-position: center;
       object-position: center;
  }

  &__img-buttons {
    display: flex;
    gap: 16px;
  }

  &__img-btn {
    display: flex;
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
  }

  &__photo-option {
    width: 100px;
    height: 100px;
    -o-object-fit: contain;
       object-fit: contain;
    -o-object-position: center;
       object-position: center;
  }

  &__navigation {
    display: flex;
    margin-bottom: 16px;
    gap: 8px;
  }

  &__navigation-item {
    font-weight: 500;
    font-size: 14px;
    color: #90a0b0;
  }

  &__navigation-link {
    text-decoration: none;
    color: inherit;
  }

  &__name {
    margin-bottom: 16px;
    font-family: $font-family;
    font-weight: 700;
    font-size: 36px;
    color: $color-text;
  }

  &__vendor {
    margin-bottom: 24px;
    font-weight: 500;
    font-size: 24px;
    color: $color-text;
  }

  &__vendor-link {
    text-decoration: none;
    text-transform: uppercase;
    color: $color-primary;
  }

  &__rating-block {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    gap: 11px;
  }

  &__price {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 24px;
    color: $color-primary;
  }

  &__characteristics-title {
    margin-bottom: 16px;
    font-family: $font-family;
    font-weight: 700;
    font-size: 16px;
    color: $color-text;
  }

  &__comments {
    display: flex;
    align-items: center;
    gap: 5px;
  }

  &__comments-count {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #ccc;
  }

  &__content {
    border-radius: 4px;
    padding: 24px 22px;
    background-color: $color-bg-accent;
  }

  &__tabs {
    display: flex;
    border-bottom: 1px solid $color-primary;
    padding-bottom: 8px;
    gap: 24px;
  }

  &__tab-btn {
    position: relative;
    border: none;
    font-family: $font-family;
    font-weight: 500;
    font-size: 16px;
    color: $color-text;
    background-color: transparent;
    cursor: pointer;
    padding-inline: 0;

    &:disabled {
      cursor: default;

      &::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: -10px;
        width: 100%;
        height: 3px;
        background-color: $color-primary;
      }
    }
  }

  &__section {
    padding-top: 32px;
  }

  &__section-title {
    display: flex;
    margin-bottom: 24px;
    font-family: $font-family;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
    color: $color-primary;
    gap: 8px;
  }

  &__description-text {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: $color-text;
  }

  &__details-block {
    display: flex;
    max-width: 500px;
  }

  &__details-block:not(:last-child) {
    border-bottom: 1px solid #d4af37;
  }

  &__details-term {
    width: 190px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: $color-text;
    padding-block: 8px;
  }

  &__details-description {
    width: 280px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: $color-text;
    padding-block: 8px;
  }

  &__feedback {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 44px;
  }

  &__feedback-comments {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }

  &__rating {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 36px;
    color: $color-text;
  }

  &__feedback-btn {
    @extend %button;
  }

  &__buy {
    position: relative;
  }

  &__buy-wrapper {
    position: sticky;
    left: 0;
    right: 0;
    top: 20px;
    display: flex;
    flex-direction: column;
    gap: 24px;
  }

  &__buy-title {
    width: 255px;
    font-family: $font-family;
    font-weight: 700;
    font-size: 24px;
    color: $color-primary;
  }

  &__buy-block {
    display: flex;
    flex-direction: column;
    padding: 24px 20px;
    background-color: $color-bg-accent;
  }

  &__buy-vendor {
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 16px;
    color: $color-text;
  }

  &__buy-text {
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 16px;
    color: $color-text;
  }

  &__buy-link {
    @extend %button;
  }

  &__modal {
    padding: 32px 48px;
  }

  &__modal-title {
    margin-bottom: 32px;
    font-family: $font-family;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    color: $color-text;
  }

  &__modal-text {
    margin-bottom: 48px;
    font-family: $font-family;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: $color-text;
  }

  &__form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 56px;
    gap: 32px;
  }

  &__form-field {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }

  &__form-text {
    width: -moz-fit-content;
    width: fit-content;
    font-family: $font-family;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: $color-text;
  }

  &__star {
    color: #d4af37;
  }

  &__short-input {
    width: 409px;
  }

  &__long-input {
    max-width: 100% !important;
  }

  &__form-star {
    border: none;
    width: 20px;
    height: 20px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: transparent;
    cursor: pointer;
  }

  &__form-stars {
    display: flex;
    gap: 24px;
  }

  &__form-textarea {
    border: 1px solid #90a0b0;
    border-radius: 4px;
    padding: 16px 32px;
    width: 100%;
    height: 56px;
    font-family: $font-family;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: $color-text;
    background-color: transparent;
    transition: border $effectDuration;
    resize: vertical;

    &::-moz-placeholder {
      font-family: $font-family;
      font-weight: 500;
      font-size: 16px;
      color: #90a0b0;
    }

    &::placeholder {
      font-family: $font-family;
      font-weight: 500;
      font-size: 16px;
      color: #90a0b0;
    }

    &:focus {
      outline: none;
      border: 1px solid $color-primary;
    }

    &--error {
      border-color: #d4af37;
      box-shadow: 0 0 4px 0 rgba(182, 98, 51, 0.5);
    }
  }

  &__modal-submit {
    display: block;
    margin-inline: auto 0;

    @extend %button;
  }

  &__modal-thank {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-block: 53px;
  }

  &__thank__title {
    margin-bottom: 30px;
    font-family: $font-family;
    font-weight: 700;
    font-size: 36px;
    color: $color-text;
  }

  &__thank-text {
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 14px;
    color: $color-text;
  }

  &__thank-btn {
    @extend %button;
  }
}
